<template>
    <div class="edit-title-component" v-if="localContent">
        <div v-if="isNewTitle" class="edit-title-component-title">
            <span class="title-highlight">{{ currentEditingTypeComputed }}</span>
        </div>
        <div v-else class="edit-title-component-title">
             <span class="title-highlight">{{ currentEditingTitle }}</span>
        </div>

        <!-- ================== Education ================== -->
        <div v-if="currentEditingType === 'education'">
            <div class="session-edit-title"></div>
            <div class="session-edit">
                <AppleStyleInput v-if="isNewTitle" id="edu-title" labelText="" inputType="text" :required="true"
                    v-model:modelValue="localContent.content.title" />

                <div class="form-line">
                    <AppleStyleInput id="edu-from" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.from_time" />
                    <AppleStyleInput id="edu-to" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.to_time" />
                </div>

                <AppleStyleInput id="edu-major" labelText="" inputType="text" :required="true"
                    v-model:modelValue="localContent.content.major" />

                <div class="form-line">
                    <AppleStyleInput id="edu-degree" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.degree" />
                    <AppleStyleInput id="edu-gpa" labelText="GPA ()" inputType="text" :required="false"
                        v-model:modelValue="localContent.content.gpa" />
                    <AppleStyleInput id="edu-city" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.city" />
                </div>

                <AppleStyleInput id="edu-honors" labelText=" ()" inputType="text" :required="false"
                    v-model:modelValue="localContent.content.honors" />

                <AppleStyleInput id="edu-courses" labelText=" ()" inputType="text" :required="false"
                    v-model:modelValue="localContent.content.courses" />
            </div>
        </div>

        <!-- ================== Work Experience ================== -->
        <div v-if="currentEditingType === 'workExperience'">
            <div class="session-edit-title"></div>
            <div class="session-edit">
                <AppleStyleInput v-if="isNewTitle" id="work-title" labelText="" inputType="text" :required="true"
                    v-model:modelValue="localContent.content.title" />

                <div class="form-line">
                    <AppleStyleInput id="work-from" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.from_time" />
                    <AppleStyleInput id="work-to" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.to_time" />
                </div>

                <div class="form-line">
                    <AppleStyleInput id="work-company" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.title" />
                    <AppleStyleInput id="work-position" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.sub_title" />
                    <AppleStyleInput id="work-city" labelText="" inputType="text" :required="false"
                        v-model:modelValue="localContent.content.city" />
                </div>
            </div>

            <div class="session-edit-title">Bullet Points</div>
            <div class="bullet-point-container" v-for="(point, index) in localContent.content.content" :key="index">
                <div class="button-container">
                    <button v-on:mouseenter="handleMouseEnter(index)" v-on:mouseleave="handleMouseLeave" type="button"
                        class="remove-button" @click="removeBulletPoint(index)">
                        x
                    </button>
                </div>
                <AppleStyleInput :id="`bullet-point-${index}`" labelText="Bullet Point" inputType="text"
                    :required="true" v-model:modelValue="point.bullet_point" />
                <AppleStyleInput :id="`bullet-content-${index}`" labelText="" inputType="text" :required="true"
                    v-model:modelValue="point.content" />
            </div>
            <button class="add-button" type="button" @click="addBulletPoint">
                + Bullet Point
            </button>
        </div>

        <!-- ================== Project Experience ================== -->
        <div v-if="currentEditingType === 'projectExperience'">
            <div class="session-edit-title"></div>
            <div class="session-edit">
                <AppleStyleInput v-if="isNewTitle" id="project-title" labelText="" inputType="text" :required="true"
                    v-model:modelValue="localContent.content.title" />

                <div class="form-line">
                    <AppleStyleInput id="project-from" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.from_time" />
                    <AppleStyleInput id="project-to" labelText="" inputType="text" :required="true"
                        v-model:modelValue="localContent.content.to_time" />
                </div>

                <AppleStyleInput id="project-role" labelText="/" inputType="text" :required="true"
                    v-model:modelValue="localContent.content.role" />
            </div>

            <div class="session-edit-title">Bullet Points</div>
            <div class="bullet-point-container" v-for="(point, index) in localContent.content.content" :key="index">
                <div class="button-container">
                    <button v-on:mouseenter="handleMouseEnter(index)" v-on:mouseleave="handleMouseLeave" type="button"
                        class="remove-button" @click="removeBulletPoint(index)">
                        x
                    </button>
                </div>
                <AppleStyleInput :id="`proj-bullet-${index}`" labelText="Bullet Point" inputType="text" :required="true"
                    v-model:modelValue="point.bullet_point" />
                <AppleStyleInput :id="`proj-content-${index}`" labelText="" inputType="text" :required="true"
                    v-model:modelValue="point.content" />
            </div>
            <button class="add-button" type="button" @click="addBulletPoint">
                + Bullet Point
            </button>
        </div>

        <!-- ================== Footer  ================== -->
        <div class="edit-title-component-footer">
            <div class="edit-cancel-btn" @click="cancelChanges"></div>
            <div class="edit-submit-btn" @click="submitChanges"></div>
        </div>
    </div>
</template>

<script>
import AppleStyleInput from '@/components/basic_ui/AppleStyleInput.vue'; // 
import metadataInstance from '@/models/metadata_model.js';

export default {
    components: {
        AppleStyleInput
    },
    name: 'EditTitleComponent',
    props: {
        currentEditingTitle: {
            type: String,
            default: ''
        },
        currentEditingType: {
            type: String,
            default: ''
        },
        isNewTitle: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        currentEditingTypeComputed() {
            if (this.currentEditingType === 'education') {
                return '';
            } else if (this.currentEditingType === 'workExperience') {
                return '';
            } else if (this.currentEditingType === 'projectExperience') {
                return '';
            } else  {
                return '';
            }
        }
    },
    data() {
        return {
            // 
            localContent: null,
            mouseHoverIndex: null
        };
    },
    mounted() {
        if (this.isNewTitle) {
            if (this.currentEditingType === 'education') {
                this.localContent = {
                    title: '',
                    content: {
                        from_time: '',
                        to_time: '',
                        major: '',
                        degree: '',
                        gpa: '',
                        city: '',
                        content: []
                    }
                }
            } else if (this.currentEditingType === 'workExperience') {
                this.localContent = {
                    title: '',
                    content: {
                        from_time: '',
                        to_time: '',
                        title: '',
                        sub_title: '',
                        city: '',
                        content: []
                    }
                }
            } else if (this.currentEditingType === 'projectExperience') {
                this.localContent = {
                    title: '',
                    content: {
                        from_time: '',
                        to_time: '',
                        title: '',
                        content: []
                    }
                }
            }
        } else {
            //  metadataInstance  localContent
            // ResumeForm.vue  setContentForType  content 
            //  { from_time: '', to_time: '', content: [] } 
            //  ResumeForm.vue 
            const originalContent = metadataInstance.contentForType(
                this.currentEditingType,
                this.currentEditingTitle
            );
            // 
            this.localContent = JSON.parse(JSON.stringify(originalContent));
        }
    },
    methods: {
        //  Bullet Point
        addBulletPoint() {
            this.localContent.content.content.push({
                bullet_point: '',
                content: ''
            });
        },
        //  Bullet Point
        removeBulletPoint(index) {
            this.localContent.content.content.splice(index, 1);
        },
        //  metadataInstance 
        submitChanges() {
            //  metadataInstance
            metadataInstance.setContentForType(
                this.currentEditingType,
                this.localContent.content,
                this.localContent.content.title,
            )
            // 
            this.$emit("changes-submitted");
        },
        //  or 
        cancelChanges() {
            // 
            this.$emit("cancel-changes");
        },
        // Hover 
        handleMouseEnter(index) {
            this.mouseHoverIndex = index;
        },
        handleMouseLeave() {
            this.mouseHoverIndex = null;
        }
    }
};
</script>

