                  v-model="work.company"
                />

                <div class="form-line">
                  <AppleStyleDatePicker
                    :id="'work-start-' + index"
                    labelText=""
                    :required="true"
                    :invalid="validationErrors.workList[index]?.startDate"
                    v-model="work.startDate"
                  />
                  <AppleStyleDatePicker
                    :id="'work-end-' + index"
